.container {
  position: relative;
  border: 1px dashed red;
  width: 100%;
  aspect-ratio: 1 / 1; /* keep square */
  padding: 0;
  overflow: hidden;
}

.layer {
  position: absolute;
  inset: 0; /* top:0; right:0; bottom:0; left:0 */
}

.layer.obstacles {
  z-index: 2; /* above grid */
}

.layer.targets {
  z-index: 3; /* above obstacles */
}

.layer.avatars {
  z-index: 4; /* above targets */
}

